<template>
  <div class="container">
    <div class="b-left">
      <div class="b-left-top">
        <div class="chart1-box">
		<div @click="returnToDashboard()" style="z-index: 10;position: absolute;cursor: pointer;left:0.25rem"><img @click="returnToDashboard()" style="opacity:0.3" width="35px"  src="../../../Content/img/back.png"></div>
          <div style="position: absolute;left:1.25rem"><span style="border-left: 5px solid #86C7E6;
          	border-right: 5px solid #86C7E6;
          	border-bottom: 5px solid #86C7E6;
          	border-top: 5px solid #86C7E6;display: inline-block;
          	"></span>&nbsp;收入</div>
          	<div style="
          	position: absolute;
          	top: 1rem;left:1.25rem;
          "><span style="border-left: 5px solid #1B85E7;
          	border-right: 5px solid #1B85E7;
          	border-bottom: 5px solid #1B85E7;
          	border-top: 5px solid #1B85E7;display: inline-block;
          	"></span>&nbsp;支出</div>
          	<div style="
          	position: absolute;
          	top: 1.5rem;left:1.25rem;
          "><span style="border-left: 5px solid red;
          	border-right: 5px solid red;
          	border-bottom: 5px solid red;
          	border-top: 5px solid red;display: inline-block;
          	"></span>&nbsp;盈亏</div>
          <div class="list-box1">
            <div class="list">
              <div class="total">
                <div class=" center-center value">{{numberComma(Math.floor(income/10000))}}</div>
                <div class=" center-center text">总收入（万元）</div>
              </div>
              <div :class="rate1>0?'status up green':'status down red'">
                <div class=" center-center value">
                  <span>{{Math.abs((rate1*100).toFixed(2))}}%</span>
                  <span class="iconfont icon-shang"></span>
                </div>
                <div class=" center-center text">
                  同比
                </div>
              </div>
            </div>
            <div class="list">
              <div class="total">
                <div class=" center-center value">{{numberComma(Math.floor(expense/10000))}}</div>
                <div class=" center-center text">总支出（万元）</div>
              </div>
              <div  :class="rate2>0?'status up green':'status down red'">
                <div class=" center-center value">
                  <span>{{Math.abs((rate2*100).toFixed(2))}}%</span>
                  <span class="iconfont icon-shang"></span>
                </div>
                <div class=" center-center text">
                  同比
                </div>
              </div>
            </div>
          </div>
          <div id="chart1"></div>
        </div>

      </div>
      <div class="b-left-bottom">
        <div class="name" style="width: 90%;z-index: 10;" :title="currentData.name.split('?')[0] + ' - ' + currentData.seriesNumber">
          {{currentData.name.split('?')[0] + ' - ' + currentData.seriesNumber}}
        </div>
        <div class="name-lower-box" >
          <div class="name-lower">
			  <span >当前位置：</span>
			  <span style="margin-top:5px;color: rgb(55, 65, 74);" :title="currentData.department">{{currentData.department}}</span>
		  </div>
          <div class="name-lower">
			  <span >安装日期：</span>
			  <span style="margin-top:5px;color: rgb(55, 65, 74);" :title="currentData.start_time.split('T')[0]">{{currentData.start_time.split('T')[0]}}</span>
		  </div>
          <div class="name-lower">
			  <span >当前状态：</span>
			  <span style="margin-top:5px;color: rgb(55, 65, 74);" :title="currentData.status">{{currentData.status}}</span>
		  </div>
		  <div class="name-lower" v-for="(list,key) of chart3.list" :key="key">
			  <span>{{list.item}}</span>
			  <span>：</span>
			  <span style="margin-top:5px;color: rgb(55, 65, 74);" :title="list.value">{{list.value}}</span>
		  </div>
        </div>

        <div id="chart3"></div>
      </div>
    </div>
    <div class="b-right">
      <div class="b-right-content">
        <div class="timeline-title" style="font-size: 0.64rem;cursor: auto;line-height: 1.2;">{{timelineTitle}}</div>
        <div class="b-timeline">
          <timeline>
            <timeline-item v-for="(item,key) in timeline" :title="item.date + ' ' + item.event" :key="key" :bg-color="item.color" @click.native="navigateTo(item)">
			<p class="timelineP" style="cursor: pointer;">{{item.date}} {{item.event}}</p>
            </timeline-item>
          </timeline>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import {equipments, radar, timeline} from '@/api/api'
  import G2 from '@antv/g2';
  import {DataSet} from '@antv/data-set'
  import {queryOverview, radarData} from '@/api/api'
  import {getRem,BASE_URL} from '@/common/common'
  import EventList from '../../components/componentEventList'
  import KpiList from '../../components/componentKpiGauge'
  import {Timeline, TimelineItem, TimelineTitle} from 'vue-cute-timeline'

  export default {
    components: {
      EventList,
      KpiList,
      Timeline,
      TimelineItem,
      TimelineTitle
    },

    data() {
      return {
        timelineTitle: '',
        timeline: [],
        //name1: this.$route.query.name,
        id: this.$route.query.id,
        currentData: {
          name: '',
          start_time: ''
        },
        name1: '',
        chart1: {
          data: [],
        },
        income: 0.00,
        incomeLast: 0.00,
        rate1:0,
        rate2: 0,
        expense: 0.00,
        expenseLast: 0.00,
        chart3: {
          data: [],
          list: [],
          chart: null,
        },
        maxCount: 0,
        radarTemplate : [{
        	"item": "维修",
        	//"value": 1
        }, {
        	"item": "巡检",
        	//"value": 3
        }, {
        	"item": "保养",
        	//"value": 7
        }, {
        	"item": "校正",
        	//"value": 7
        }, {
        	"item": "强检",
        	//"value": 5
        }],
      }
    },
    computed: {},
    created() {
      // this.name2 = this.chart1.data[0].name
      // this.getqueryOverview()
      this.getChart1().then(() => {
        //默认选中第一条数据
		if(!this.chart1.data.length)
			this.currentData ={
				ID:0,
				name: "",
				brand: "",
				model: "",
				value: "",
				seriesNumber: "",
				department: "",
				start_time: "",
				status: "",
			}
		else
			this.currentData = this.chart1.data[0]
        // 获取chart1之后获取雷达
        this.renderChart1()
        this.getChart3().then(() => {
          this.renderChart3()
        })
        // 获取timeline
        this.getTimeline()
      })
    },
    methods: {
      navigateTo: function(item){
		if (item.id != 0)
			window.location.href= BASE_URL+"/Dispatch/DispatchResponse?dispatchID="+item.id+"&requestType="+item.requestType;
      },
	  returnToDashboard: function() {
	  	this.$router.push("indexU");
	  },
      getChart1() {
        // this.name1
        return new Promise(resolve => {
            equipments({id: this.id}).then(res => {
              res.Data.map((n, i) => {
				if(n.Incomes==0 && n.Expenses==0)
					return 
                this.income += n.Incomes;
                this.expense += n.Expenses;
                this.incomeLast += n.LastIncomes;
                this.expenseLast += n.LastExpenses;
                this.chart1.data.push(Object.assign({}, n, {
                  name: n.Name + ' - ' + n.Manufacturer.Name + ' - ' + n.EquipmentCode + '?' + i,
                  '收入': Number(n.Incomes/10000),
                  '支出': Number(n.Expenses/10000),
                  brand: n.Manufacturer.Name,
                  model: n.EquipmentCode,
                  value: Number(n.PurchaseAmount/10000),
                  seriesNumber: n.AssetCode,
                  department: n.Department.Name,
                  start_time: n.InstalStartDate,
                  status: n.EquipmentStatus.Name
                }))
              })
              this.expenseLast==0?this.rate2=0:this.rate2=(this.expense-this.expenseLast)/this.expenseLast;
              this.incomeLast==0?this.rate1=0:this.rate1=(this.income-this.incomeLast)/this.incomeLast;

          resolve()
          })
        })
      },
      random(lower, upper) {
        return Math.floor(Math.random() * (upper - lower)) + lower;
      },
      getChart3() {
        let _this = this
        return new Promise(resolve => {
          radarData({id: this.currentData.ID}).then(res => {
            var radar_data = this.radarTemplate;
            radar_data[0].value = res.Data.Repair;
            radar_data[1].value = res.Data.Maintain;
            radar_data[2].value = res.Data.Inspection;
            radar_data[3].value = res.Data.OnSiteInspection;
            radar_data[4].value = res.Data.Correcting;
            var max = 0;
            for(var i=0; i<radar_data.length; i++){
              if (max<radar_data[i].value) {
                max = radar_data[i].value
              }
            }
            _this.maxCount = max;
            _this.maxTickCount = ((max > 5) ? 5 : ((max < 2) ? 2 : max));
            _this.chart3.list = radar_data;
            var _DataSet = DataSet,
              DataView = _DataSet.DataView;
            var dv = new DataView().source(_this.chart3.list);
            dv.transform({
              type: 'fold',
              fields: ['value'], // 展开字段集
              key: 'user', // key字段
              value: 'score' // value字段
            });
            this.chart3.data = dv
            resolve(dv)
          })
        })
      },
      itemColor(type) {
        switch (type) {
          case 1:
          case 3:
            return '#E64340';
            break;
          case 4:
          case 2:
          case 5:
            return '#3eaf7c'
            break;
          default:
            return '#f0f0f0'
            break;
        }
      },
      getTimeline() {
        timeline({id: this.currentData.ID}).then(res => {
		  if(this.currentData.ID!=0){
                    this.timelineTitle = res.Data.Name+' - '+res.Data.EquipmentCode
          var timelineEvent = res.Data.Dispatches.map((n, i) => {
            return {
              date: n.EndDate.split('T')[0],
              event: n.TimelineDesc,
              color: this.itemColor(n.RequestType.ID),
			  id:n.ID,
			  requestType:n.RequestType.ID,
            }
          });
          this.timeline = timelineEvent
			}
			else
				this.timeline=[]
        })
        //this.timelineTitle = TIMELINE.equipment;
        //this.timeline = TIMELINE.timeline;
      },
      renderChart1() {
        let _this = this
        var dv = new DataSet.DataView();
        dv.source(this.chart1.data).transform({
          type: 'map',
          callback: function callback(row) {
            row['支出'] *= -1;
            return row;
          }
        })
          .transform({
            type: 'fold',
            fields: ['收入', '支出'],
            key: 'opinion',
            value: 'value',
            retains: ['name']
          });

              var colorMap = {
          '收入': '#86C7E6',
          '支出': '#1B85E7',
        };

        var chart = new G2.Chart({
          container: 'chart1',
		  forceFit: _this.chart1.data.length <= 20 ? true : false,
          height: document.querySelector('#chart1').clientHeight,
		  width: _this.chart1.data.length * 48,
          padding: [getRem(1.6), 0, 0, 0],
          // animate: false,
        });
        chart.source(dv);
        chart.axis(false);


        this.chart1.data.forEach(function (data, dataIndex) {

          if (data['支出'] > data['收入']) {
            // 辅助框
            chart.guide().regionFilter({
              top: true, // 指定 giude 是否绘制在 canvas 最上层，默认为 false, 即绘制在最下层
              start: [dataIndex - .5, -data['收入']], // 辅助框起始位置，值为原始数据值，支持 callback
              end: [dataIndex + .5, -data['支出']],// 辅助框结束位置，值为原始数据值，支持 callback
              color: '#E2340D',
            });
          }
        });


        chart.tooltip({
          inPlot: true,
          useHtml: true,
          htmlContent: function (title, items) {
            let data = {};
            _this.chart1.data.forEach(n => {
              if (n.name === title) {
                data = n
              }
            })
            return `<div class="g2-tooltip">
                    <div class="g2-tooltip-title">${title.split('?')[0]}</div>
                    <ul class="g2-tooltip-list">
                      <li class="g2-tooltip-li">设备价值：${_this.numberComma(Math.floor(Number(data.value)))}万</li>
                      <li class="g2-tooltip-li">型号：${_this.numberComma(data.model)}</li>
                      <li class="g2-tooltip-li">品牌：${_this.numberComma(data.brand)}</li>
                      <li class="g2-tooltip-li">收入：${_this.numberComma(Math.floor(Number(data.收入)))}万</li>
                      <li class="g2-tooltip-li">支出：${_this.numberComma(Math.floor(Number(data.支出)))}万</li>
                    </ul>
                  </div>`
          }
        });
        // ())

//         chart.legend({
//           custom: true,
//           itemWidth: getRem(100),
// 		  autoWrap:true,
//           items: [
//             {
//               value: $.i18n.prop("dashboard.radar.income"), // 图例项的文本内容
//               marker: {
//                 symbol: 'square',  // 该图例项 marker 的形状，参见 marker 参数的说明
//                 fill: colorMap['收入'],  // 该图例项 marker 的填充颜色
//                 radius: getRem(.1)
//               }
//             },
//             {
//               value: $.i18n.prop("dashboard.radar.expense"), // 图例项的文本内容
//               marker: {
//                 symbol: 'square',  // 该图例项 marker 的形状，参见 marker 参数的说明
//                 fill: colorMap['支出'],  // 该图例项 marker 的填充颜色
//                 radius: getRem(.1)
//               }
//             },
//             {
//               value: $.i18n.prop("dashboard.radar.difference"), // 图例项的文本内容
//               marker: {
//                 symbol: 'square',  // 该图例项 marker 的形状，参见 marker 参数的说明
//                 fill: 'red',  // 该图例项 marker 的填充颜色
//                 radius: getRem(.1)
//               }
//             },
//           ],
//           position: 'top-left',
//           offsetX: getRem(1.2),
//           offsetY: getRem(-0.4),
//           // marker: {fill: 'red'},
//           textStyle: {
//             // textAlign: 'center', // 文本对齐方向，可取值为： start middle end
//             fill: '#000', // 文本的颜色
//             fontSize: getRem(.36), // 文本大小
//             // textBaseline: 'top' // 文本基准线，可取 top middle bottom，默认为middle
//           },
// 
// 
//           hoverable: false,
//           reactive: false,
//           clickable: false,
//           // title: {
//           //   textAlign: 'center', // 文本对齐方向，可取值为： start middle end
//           //   // fill: '#404040', // 文本的颜色
//           //   fontSize: '50', // 文本大小
//           //   // fontWeight: 'bold', // 文本粗细
//           //   textBaseline: 'top' // 文本基准线，可取 top middle bottom，默认为middle
//           // }
//           // autoWrap: false,
//         });

        // chart.axis(false);
        chart.coord();
        chart.intervalStack().position('name*value').color('opinion', function (opinion) {
          return colorMap[opinion];
        });

        chart.on('click', ev => {
          // 获取逼近的点 point 的原始数据集合
          var records = chart.getSnapRecords({x: ev.x, y: ev.y});

          // const data = ev.data;
          const name = records[0]._origin.name
          var targetItem = _this.chart1.data.find(function (val) {
            return val.name === name;
          })
          _this.currentData = targetItem
          //获取雷达数据
          _this.getChart3().then(()=>{
				_this.renderChart3()
			}
		  )
          //获取timeline
          _this.getTimeline()
        });

        chart.render();
      },

      renderChart3() {
        let _this = this
		document.querySelector('#chart3').innerHTML=""
        _this.chart3.chart = new G2.Chart({
          container: 'chart3',
          forceFit: true,
          height: document.querySelector('#chart3').clientHeight,
          padding: [getRem(.5), getRem(.2), 0, getRem(.1)]
        });
        _this.chart3.chart.source(_this.chart3.data, {
          score: {
            min: 0,
            max: _this.maxCount,
            tickCount: _this.maxTickCount  //radar 
          }
        });
        _this.chart3.chart.coord('polar', {
          radius: 0.6
        });
        _this.chart3.chart.axis('item', {
          line: null,
          label: {
            // offset: {number}, // 设置坐标轴文本 label 距离坐标轴线的距离
            textStyle: {
              // textAlign: 'center', // 文本对齐方向，可取值为： start middle end
              // fill: '#404040', // 文本的颜色
              fontSize: getRem(.3), // 文本大小
              fontWeight: 'bold', // 文本粗细
              // rotate: 30,
              // textBaseline: 'top' // 文本基准线，可取 top middle bottom，默认为middle
            },
            // autoRotate: {boolean} // 是否需要自动旋转，默认为 true
          },
          tickLine: null,
          grid: {
            lineStyle: {
              lineDash: null
            },
            hideFirstLine: false
          }
        });
        _this.chart3.chart.axis('score', {
          // title: null,
          label: null,
          line: null,
          tickLine: null,
          grid: {
            type: 'polygon',
            lineStyle: {
              lineWidth: getRem(.02),
              lineDash: null
            }
          }
        });
        _this.chart3.chart.legend(false)
        _this.chart3.chart.tooltip({
          // showTitile: false,
          'g2-tooltip': {
            'background-color': 'rgba(0, 0, 0, 0.7)',
            color: '#ddd',
          },
          itemTpl: '<li style="font-size: .4rem;">次数：{value}</li>'
        });
        _this.chart3.chart.line().position('item*score').color('user').size(getRem(.01));
        // chart.point().position('item*score').color('user').shape('circle').size(getRem(.04)).style({
        //   stroke: '#fff',
        //   lineWidth: 1,
        //   fillOpacity: 1
        // });
        _this.chart3.chart.area().position('item*score').color('user');
        // chart.active(false)
        // const geoms = chart.getAllGeoms();
        // for (let i = 0; i < geoms.length; i++) {
        //   const geom = geoms[i];
        //   geom.select(false)
        // }
        _this.chart3.chart.render();
      },
      numberComma(source, length = 3) {
        source = String(source).split(".");
        source[0] = source[0].replace(new RegExp('(\\d)(?=(\\d{' + length + '})+$)', 'ig'), "$1,");
        return source.join(".");
      },
    }
  }
</script>
<style scoped lang="less">
	
		/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
	::-webkit-scrollbar
	{
	    width: 0.32rem;
	    height: 0.32rem;
	    background-color: #F5F5F5;
	}
	 
	/*定义滚动条轨道 内阴影+圆角*/
	::-webkit-scrollbar-track
	{
	    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	    border-radius: 0.2rem;
	    background-color: #F5F5F5;
	}
	 
	/*定义滑块 内阴影+圆角*/
	::-webkit-scrollbar-thumb
	{
	    border-radius: 0.2rem;
	    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	}
	
  .container {
    min-width: 1200px;
    width: 100%;
    height: 100%;
    background: #D7E0EE;
    overflow: hidden;
    font-size: .4rem;
    padding: .3rem;
    display: flex;

    .b-left {
      width: 50%;
      height: 100%;
      display: flex;
      flex-direction: column;

      .b-left-top {
        border-radius: .16rem;
        height: 50%;
        margin: .15rem;
        background: #fff;

        .chart1-box {
          background: #fff;
          /*flex: 1;*/
          padding: .5rem;
          position: relative;
          border-radius: .16rem;
          height: 100%;

          .name {
            position: absolute;
            font-size: .5rem;
            font-weight: bold;
          }

          .list-box1 {
            position: absolute;
            right: 0;
            left: 0;
            padding-right: .2rem;
            top: .5rem;
            display: flex;
            justify-content: flex-end;
            z-index: 2;

            .list {
              display: flex;
              align-items: baseline;
              margin-left: .2rem;

              .total {
                margin-right: .2rem;

                .value {
                  font-weight: bold;
                  font-size: .6rem;
                  margin-bottom: .1rem;
                }

                .text {
                  font-size: .4rem;
                }
              }

              .status {


                .value {
                  font-weight: bold;
                  font-size: .6rem;
                  display: flex;
                  align-items: baseline;
                  margin-bottom: .1rem;

                  .iconfont {
                    font-size: .7rem;
                  }
                }

                .text {
                  color: #000;
                  font-size: .4rem;
                }

                  &.up {}
				  
				  &.red{color: #E64340;}
				  &.green{color: #3eaf7c;}
				  
                  &.down {
                    
                    .iconfont {
                      transform: rotate(180deg);
                    }
                  }
              }
            }
          }

          #chart1 {
            width: 100%;
            height: 100%;
							overflow-x: auto;
							overflow-y: hidden;
          }
        }

      }

      .b-left-bottom {
        border-radius: .16rem;
        height: 50%;
        margin: .15rem;
        background: #fff;
        display: flex;
        position: relative;

        .name {
          position: absolute;
          left: .5rem;
          top: .5rem;
          font-size: 0.4rem;
          font-weight: bold;
		  white-space:nowrap; //强制不换行
		  overflow:hidden; //自动隐藏文字
		  text-overflow: ellipsis; //文字隐藏后添加省略号
		  padding: 3px 0px;
        }

        .name-lower-box {
          display: flex;
          width: 48%;
          justify-content: center;
          flex-direction: column;
          font-size: 0.36rem;
          padding-left: .5rem;

          .name-lower {
		  white-space:nowrap; //强制不换行
		  overflow:hidden; //自动隐藏文字
		  text-overflow: ellipsis; //文字隐藏后添加省略号
		  padding: 8px 0px;
          }
        }

        #chart3 {
          width: 50%;
          height: 100%;
        }

        .list3 {
          width: 25%;
          font-weight: bold;
          display: flex;
          flex-direction: column;
          justify-content: center;
          padding-left: .3rem;

          .item {
            margin: .3rem 0;

            .title {
            }

            .count {
				margin-right: .5rem;
            }
          }


        }
      }
    }

    .b-right {
      width: 50%;
      height: 100%;
      padding: .15rem;

      .b-right-content {
        width: 100%;
        height: 100%;
        border-radius: .16rem;
        background: #fff;
        padding: .8rem;
        //overflow-y: auto;

        .b-timeline {
          width: 100%;
          height: 95%;
          overflow-y: auto;

          /deep/ .timeline {
            margin: 0.5rem 1rem;

            &:after {
              top: 0rem;
              height: auto;
              bottom: .7rem;
            }

            .timeline-title {
              font-size: .6rem;
              font-weight: bold;
              margin: -.15em 0 .5rem 0;
              cursor: unset;
            }

            .timeline-item {
              margin: 0.5em 0 0 .5rem;
			  padding-bottom: 0.5em;
			  border-bottom: 0px dotted;
            }
			.timelineP{
				white-space:nowrap; //强制不换行
				overflow:hidden; //自动隐藏文字
				text-overflow: ellipsis; //文字隐藏后添加省略号
				width: 100%;
				padding-bottom: 3px;
			}

            .timeline-circle {
              top: 0;
              width: .3rem;
              height: .3rem;
              left: -.65rem;
            }

            .timeline-title-circle {
              width: 0;
              height: 0;
              display: none;
            }
          }
        }

      }
    }


    /deep/ .g2-tooltip {
      z-index: 99;
      position: absolute;
      background-color: rgba(0, 0, 0, 0.7);
      border-radius: .16rem;
      color: #ddd;
	  white-space: nowrap;
      font-size: .36rem;
      line-height: 1.2;
      padding: .24rem .24rem .1rem .24rem;
      box-shadow: 0 0 .1rem #aeaeae;
    }

    /deep/ .g2-tooltip-title {
      font-size: .4rem;
    }

    /deep/ .g2-tooltip-list {
      padding: 0;
      list-style-type: none;
      margin-top: .2rem;

      .g2-tooltip-li {
        margin: .2rem 0;
      }
    }

    canvas {
      background: #fff;
      width: 100%;
      height: 100%;
    }

  }


</style>




